*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}


body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}


/* Main */

body {
  font-family: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  color: rgb(35, 35, 35);
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: rgb(244, 244, 244);
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.2rem 2rem;
  box-shadow: 0 0px 4px rgb(63, 63, 63);
  background-color: white;
}

.logo {
  font-size: 1.8rem;
  font-weight: 700;
}

nav {
  display: flex;
  gap: 36px;
}

nav button {
  border: none;
  background-color: transparent;
  font-size: 1.5rem;
  font-weight: 500;
  cursor: pointer;
}

nav button:hover {
  filter: opacity(0.5);
}

.active {
  border-bottom: 4px solid rgb(0, 119, 255);
  color: rgb(0, 119, 255);
}

#content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  background-color: white;
  max-width: 80%;
  border-radius: 24px;
  box-shadow: 0 0 4px rgb(161, 161, 161);
  padding: 36px;
}

.card h1 {
  font-weight: 700;
  font-size: 2.4rem;
}

.card > p {
  font-weight: 400;
  font-size: 1.3rem;
  margin: 12px 0 28px;
  color: rgb(106, 106, 106);
}

.info {
  display: flex;
  gap: 72px;
}

.info > div {
  flex: 1;
}

.info h3 {
  font-size: 1.6rem;
  font-weight: 500;
}

.info p {
  font-weight: 400;
  font-size: 1.15rem;
  margin: 12px 0;
  color: rgb(106, 106, 106);
}

.card > div:last-child {
  font-weight: 400;
  font-size: 1.15rem;
  margin: 18px 0 0;
  color: rgb(106, 106, 106);
}


footer {
  text-align: center;
  padding: 1.2rem;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,cAAc;;AAEd;EACE,sBAAsB;AACxB;;AAEA;EACE,SAAS;EACT,UAAU;AACZ;;;AAGA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;EACE,cAAc;EACd,eAAe;AACjB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;AACA;EACE,kBAAkB;AACpB;;;AAGA,SAAS;;AAET;EACE,kIAAkI;EAClI,sBAAsB;EACtB,aAAa;EACb,sBAAsB;EACtB,iBAAiB;EACjB,oCAAoC;AACtC;;AAEA;EACE,aAAa;EACb,8BAA8B;EAC9B,mBAAmB;EACnB,oBAAoB;EACpB,qCAAqC;EACrC,uBAAuB;AACzB;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,SAAS;AACX;;AAEA;EACE,YAAY;EACZ,6BAA6B;EAC7B,iBAAiB;EACjB,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,oBAAoB;AACtB;;AAEA;EACE,yCAAyC;EACzC,uBAAuB;AACzB;;AAEA;EACE,OAAO;EACP,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,uBAAuB;EACvB,cAAc;EACd,mBAAmB;EACnB,sCAAsC;EACtC,aAAa;AACf;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,gBAAgB;EAChB,iBAAiB;EACjB,mBAAmB;EACnB,yBAAyB;AAC3B;;AAEA;EACE,aAAa;EACb,SAAS;AACX;;AAEA;EACE,OAAO;AACT;;AAEA;EACE,iBAAiB;EACjB,gBAAgB;AAClB;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,cAAc;EACd,yBAAyB;AAC3B;;AAEA;EACE,gBAAgB;EAChB,kBAAkB;EAClB,gBAAgB;EAChB,yBAAyB;AAC3B;;;AAGA;EACE,kBAAkB;EAClB,eAAe;AACjB","sourcesContent":["/* Resetting */\n\n*, *::before, *::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n  padding: 0;\n}\n\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg, picture, video, canvas, svg {\n  display: block;\n  max-width: 100%;\n}\n\ninput, button, textarea, select {\n  font: inherit;\n}\n\np, h1, h2, h3, h4, h5, h6 {\n  overflow-wrap: break-word;\n}\n\np {\n  text-wrap: pretty;\n}\nh1, h2, h3, h4, h5, h6 {\n  text-wrap: balance;\n}\n\n\n/* Main */\n\nbody {\n  font-family: system-ui, \"Segoe UI\", Roboto, Helvetica, Arial, sans-serif, \"Apple Color Emoji\", \"Segoe UI Emoji\", \"Segoe UI Symbol\";\n  color: rgb(35, 35, 35);\n  display: flex;\n  flex-direction: column;\n  min-height: 100vh;\n  background-color: rgb(244, 244, 244);\n}\n\nheader {\n  display: flex